@import "_variables";
@import "layout";
@import "_header";
@import "_component";
@import "_drawer";
@import "login";
@import "table";
@import "modal-verify";

body {
  font-family: "Roboto", sans-serif;
}

.hide {
  display: none !important;
}

.block {
  display: block !important;
}

.center-block {
  text-align: center;
  display: block;
}

.center-margin-block {
  margin: auto;
  display: block;
}

.bottom-close-modal {
  @extend .center-block;
  color: $color-mandalay;
  cursor: pointer;
  &:hover {
    opacity: 0.7;
  }
}

.full-width,
.full-width .MuiFormControl-root {
  width: 100%;
}


@for $i from 0 through 100 {
  .mt-#{$i} {
    margin-top: #{$i}px !important;
  }
  .mr-#{$i} {
    margin-right: #{$i}px !important;
  }
  .mb-#{$i} {
    margin-bottom: #{$i}px !important;
  }
  .ml-#{$i} {
    margin-left: #{$i}px !important;
  }
}

a.btn-export:-webkit-any-link {
  color: transparent;
  &:active, &:focus {
    color: transparent;
  }
}

p.value-arr {
  margin-top: 0;
  margin-bottom: 5px;
}

.box-empty-wrap {
  text-align: center;
  img {
    margin-bottom: 5px;
    margin-top: 25px;
  }
}